
















	export default {
		data() {
			return {
				title: 'Hello'
			}
		},
		onLoad() {

		},
		methods: {
           shareimg(){
			   // 发起用户授权
			   uni.getSetting({
				success(res) {
					if(!res.authSetting['scope.writePhotosAlbum']){
						uni.authorize({
							scope:'scope.writePhotosAlbum',
							success() {
								console.log('授权成功')
							}
						})
					}else{
						uni.navigateTo({
							url:'../shareimg/shareimg'
						})
					}
				}
			   })
		   }
		}
	}
